@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.card {
  background: rgba(238, 255, 2, 0.945);
  border-radius: 10px;
  height: 250px;
  margin: 1rem;
  position: relative;
  display: table;
  width: 250px;
  box-shadow: 0 2px 15px rgb(158, 2, 132), 0 10px 15px rgb(255, 153, 238);
}

.card > .img-container {
  text-align: center;
  vertical-align: middle;
  display: table-cell;
  overflow: hidden;

  
}
.card > .img-container > img {
  max-width: 90%;
  max-height: 225px;
      
}

.card:hover {
  box-shadow: 0 3px 20px rgb(117, 255, 89), 0 0px 10px rgb(117, 255, 89);
  -webkit-transition: box-shadow .25s ease;
  transition: box-shadow .25s ease;
  -webkit-transition: border .15s ease;
  transition: border .15s ease;
  border: 3px solid rgba(249, 79, 255, 0.541);
}


/*hover properties*/
.img-hover-zoom {
  overflow: hidden; 
}

.img-hover-zoom img {
  -webkit-transition: -webkit-transform .70s ease;
  transition: -webkit-transform .70s ease;
  transition: transform .70s ease;
  transition: transform .70s ease, -webkit-transform .70s ease;
}

.img-hover-zoom:hover img {
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
}
.wrapper {

  height: 100%;
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
  background: rgba(0, 0, 0, 0.9);
  background: radial-gradient(circle, rgba(0, 0, 0, 0.97) 10%, rgba(78, 0, 109, 0.97) 64%, rgba(0, 0, 0, 0.97) 99%); 
  padding: 20px;
  margin-bottom: 20px;
  width: 100%;
  border-bottom: 2px solid;
  border-color: springgreen;
}

.title {
 
  font-size: 3rem;
  text-align: center;
  text-shadow: 1px 2px rgb(0, 92, 92);
  color:rgb(255, 255, 167);
  margin-bottom: 50px;
  
}

.scores {
  font-size: 1rem;
  color: whitesmoke;
  text-align: right;

}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  
  
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  
}

html { 
  background: url(/click-game/static/media/athf_bg.eee679af.jpeg) no-repeat center center fixed;
  background-size: cover;
}

